document.addEventListener("DOMContentLoaded", function () {
    navegacionSelecion();
});

function navegacionSelecion(){
    const navegacion = document.querySelectorAll(".navegacion a");

    const header = document.querySelector(".header");

    /* IDs */
    const Inicio = document.querySelector("#Inicio");
    const Sobre_Mi = document.querySelector("#Sobre_Mi");
    const Servicios = document.querySelector("#Servicios");
    const Portafolio = document.querySelector("#Portafolio");
    const Contacto = document.querySelector("#Contacto");

    navegacion[0].classList.add("seleccion");
    
    window.addEventListener("scroll", function() {
        if(Inicio.getBoundingClientRect().top < 1){
            navegacion[0].classList.add("seleccion");
        }
        else {
            navegacion[0].classList.remove("seleccion");
        }
        if(Sobre_Mi.getBoundingClientRect().top < 100){
            navegacion[0].classList.remove("seleccion");
            navegacion[1].classList.add("seleccion")
            header.classList.add("sombra")
        }
        else {
            navegacion[1].classList.remove("seleccion");
            header.classList.remove("sombra");
        }
        if(Servicios.getBoundingClientRect().top < 1){
            navegacion[1].classList.remove("seleccion");
            navegacion[2].classList.add("seleccion")
        }
        else {
            navegacion[2].classList.remove("seleccion");
        }
        if(Portafolio.getBoundingClientRect().top < 1){
            navegacion[2].classList.remove("seleccion");
            navegacion[3].classList.add("seleccion")
        }
        else {
            navegacion[3].classList.remove("seleccion");
        }
        if(Contacto.getBoundingClientRect().top < 1){
            navegacion[3].classList.remove("seleccion");
            navegacion[4].classList.add("seleccion")
        }
        else {
            navegacion[4].classList.remove("seleccion");
        }
    });
}